.main-header {
  min-height: 65px;
}

.main-header__logo {
  display: flex;
  align-items: center;
  padding-top: 12px;
  padding-bottom: 16px;
  border-bottom: 1px solid #e6e6e6;

  &:hover {
    opacity: 0.8;
  }

  &:active {
    opacity: 0.6;
  }
}

.main-header__logo-image {
  display: block;
  margin-right: 55px;
  margin-left: 20px;
}


@media (min-width: @tablet-width) {
  .main-header {
    display: flex;
    padding: 0 30px;
  }

  .main-header__logo {
    display: block;
    margin-right: 93px;
    margin-top: 24px;
    border-style: none;
    padding: 0;
  }

  .main-header__logo-image {
    width: 174px;
    height: auto;
    margin: 0;
  }

  .main-header__logo-text {
    display: none;
  }
}


@media (min-width: @desktop-width) {
  .main-header {
    padding: 0 0 0 110px;
    align-items: flex-start;
  }

  .main-header__logo {
    margin-top: 53px;
    margin-bottom: 74px;
  }

  .main-header__logo-image {
    width: 202px;
  }
}
