.bounty {
  position: relative;
  padding: 0 20px;
  width: 100%;
  height: 290px;
  background: url("../img/bounty-bg-image.jpg") no-repeat 20px;
  box-sizing: border-box;
  margin-bottom: 25px;
}
@media (min-resolution: 2dppx) {
  .bounty {
    background-image: url("../img/bounty-bg-image@2x.jpg");
    background-size: 280px auto;
  }
}
.bounty::after {
  position: absolute;
  content: "";
  width: calc(100% - 40px);
  height: 100%;
  background-color: rgba(104, 183, 56, 0.85);
  z-index: 1;
}
.bounty::before {
  position: absolute;
  top: 58px;
  left: 50%;
  transform: translateX(-50%);
  content: "";
  width: 78px;
  height: 80px;
  z-index: 2;
  background: url("../img/bounty-icon.svg") no-repeat;
}
.bounty__title {
  position: absolute;
  left: 78px;
  top: 65%;
  display: block;
  font-weight: 400;
  font-size: 16px;
  line-height: 20px;
  text-align: center;
  color: #ffffff;
  width: 161px;
  margin: 0;
  z-index: 2;
}
@media (min-width: 768px) {
  .bounty {
    height: 200px;
    padding: 0;
    background-size: cover;
    background-position: 0 60%;
    margin-bottom: 76px;
  }
  .bounty::after {
    width: 100%;
  }
  .bounty::before {
    top: 50%;
    left: 77px;
    transform: translateY(-50%);
    width: 80px;
    height: 82px;
  }
  .bounty__title {
    top: 50%;
    left: auto;
    right: 124px;
    transform: translateY(-50%);
    text-align: left;
  }
}
@media (min-width: 1440px) {
