    margin-top: 32px;
  }
  .bounty::after {
    width: 100%;
  }
  .bounty::before {
    top: 55px;
    left: 50%;
    transform: translateX(-50%);
  }
  .bounty__title {
    top: auto;
    right: 43px;
    bottom: 10px;
    text-align: center;
  }
